.about-container {
    width: 100%;
    height: 100%;
    padding-left: 20vh;
    padding-right: 20vh;
    
}

.contenedor-sobre-mi {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.about-desc{
    width: 50%;
}

@media (max-width:760px){
    .about-container {
        width: 100%;
        height: 100%;
        padding-left: 0vh;
        padding-right: 0vh;
    }
    
    .contenedor-sobre-mi {
        display: flex;
        /* flex-direction: column; */
        justify-content: space-between;
        align-items: center;
        width: 98%;
        padding: 10px;
    }
    .about-desc{
        width: 98%;
        overflow-wrap: break-word;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    .about-img{
        margin-right: 20px;
    }
    .imagen-sobre-mi{
        margin-right: 10px;
    }
   
}
.contenedor-proyectos {
    margin-top: 20px;
    width: 100%;
    padding: 50px;
    min-height: 400px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.p-proyects {
    color: rgb(27, 3, 27);
}

@media (max-width:760px) {
    .contenedor-proyectos {
        width: 100%;
        padding: 50px;
        min-height: 400px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
    }

    .contenedor-envelop {
        display: none !important;


    }
    .version-movil{
        background-color: white !important;
    }

    .card-version-movil-column {
        margin: 10px;
    }
}
