.h1-custom-3 {
    position: relative;
    background: linear-gradient(to right, #ececff, #dde9ff, #d0caff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.h1-custom-3:before,
.h1-custom-3:after {
    content: attr(data-text);
    position: absolute;
    top: 100;
    left: 0;
}

.h1-custom-3:before {
    z-index: -1;
    text-shadow: -0.001em -0.001em 1px rgba(255, 255, 255, 0.15);
